<template>
	<div id="new-employee">
		<h3>New Employee</h3>
		<div class="row">
			<form @submit.prevent="saveEmployee" class="col s4">
				<div class="row">
					<div class="input-field col s12">
						<input type="text" v-model="employee_id" placeholder="Employee ID" required>
					</div>
				</div>
				<div class="row">
					<div class="input-field col s12">
						<input type="text" v-model="name" placeholder="Name" required>
					</div>
				</div>

				<div class="row">
					<div class="input-field col s12">
						<input type="text" v-model="dept" placeholder="Department" required>
					</div>
				</div>
				<div class="row">
					<div class="input-field col s12">
						<input type="text" v-model="position" placeholder="Position" required>
					</div>
				</div>

				<button type="submit" class="btn green darken-2">Insert Data</button>
				<router-link to="/" class="btn red" style="margin-left: 10px">Cancel</router-link>
			</form>
		</div>
	</div>
</template>

<script>
	import db from './firebaseInit'
	export default {
		name: "new-employee",
		data() {
			return {
				// initialize first with null
				employee_id: null,
				name: null,
				dept: null,
				position: null
			}
		},
		methods: {
			saveEmployee(){
				db.collection('employees').add({
					employee_id: this.employee_id,
					name: this.name,
					dept: this.dept,
					position: this.position
				}).then(docRef => {
					this.$router.push('/')
				}).catch(error => console.log(error))
			}
		}
	}
</script>

