<template>
	<div id="edit-employee">
		<h5>Update Employee Details</h5>

		<div class="row">
			<form @submit.prevent="updateEmployee" class="col s4">
				<div class="row">
					<div class="input-field col s12">
						<input disabled type="text" v-model="employee_id" placeholder="Employee ID" required>
					</div>
				</div>
				<div class="row">
					<div class="input-field col s12">
						<input type="text" v-model="name" placeholder="Name" required>
					</div>
				</div>

				<div class="row">
					<div class="input-field col s12">
						<input type="text" v-model="dept" placeholder="Department" required>
					</div>
				</div>
				<div class="row">
					<div class="input-field col s12">
						<input type="text" v-model="position" placeholder="Position" required>
					</div>
				</div>

				<button type="submit" class="btn blue darken-2">Update Data</button>
				<router-link to="/" class="btn red" style="margin-left: 10px">Cancel</router-link>
			</form>
		</div>

	</div>

</template>

<script>
	import db from './firebaseInit'

	export default {
		name: "edit-employee",
		data() {
			return {
				// initialize first with null
				employee_id: null,
				name: null,
				dept: null,
				position: null
			}
		},
		beforeRouteEnter(to, from, next) {

			// before the page loads, get the data
			db.collection('employees').where('employee_id', '==', to.params.employee_id).get()
				.then(querySnapshot => {
					querySnapshot.forEach(doc => {
						next(vm => {
							vm.employee_id = doc.data().employee_id
							vm.name = doc.data().name
							vm.dept = doc.data().dept
							vm.position = doc.data().position
						})
					})
				})
		},
		watch: {
			'$route': "fetchData"
		},
		methods: {
			fetchData() {
				// fetch the data from router
				db.collection('employees').where('employee_id', '==', this.$route.params.employee_id)
					.get().then(querySnapshot => {
					querySnapshot.forEach(doc => {
						this.employee_id = doc.data().employee_id
						this.name = doc.data().name
						this.dept = doc.data().dept
						this.position = doc.data().position
					})
				})
			},
			updateEmployee() {
				// update the data
				db.collection('employees').where('employee_id', '==', this.$route.params.employee_id)
					.get().then(querySnapshot => {
					querySnapshot.forEach(doc => {
						doc.ref.update({
							employee_id: this.employee_id,
							name: this.name,
							dept: this.dept,
							position: this.position
						}).then(() => {
							this.$router.push({name: 'view-employee', params: {employee_id: this.employee_id}})
						})
					})
				})
			}
		}
	}
</script>

