<template>
	<div id="dashboard">
		<h3>Dashboard</h3>

		<ul class="collection with-header">
			<li class="collection-header">
				<h5>Employee Names</h5>
			</li>
			<router-link v-for="employee in employees" v-bind:key="employee.id"
			             v-bind:to="{name: 'view-employee', params: {employee_id: employee.employee_id}}">
				<li class="collection-item" style="color: black">
					{{employee.employee_id}} - {{employee.name}}
					<i class="fa fa-chevron-circle-right secondary-content"></i>
					<hr style="margin: 10px 0">
				</li>
			</router-link>

		</ul>

		<div class="fixed-action-btn">
			<router-link to="/new" class="btn-floating btn-large red">
				<i class="fa fa-plus"></i>
			</router-link>
		</div>
	</div>
</template>

<script>
	import db from "./firebaseInit"

	export default {
		name: "dashboard",
		data() {
			return {
				employees: []
			}
		},
		created() {
			db.collection("employees").orderBy('employee_id').get().then(querySnapshot => {
				querySnapshot.forEach(doc => {
					// console.log(doc.data())
					const data = {
						"id": doc.id,
						"employee_id": doc.data().employee_id,
						"name": doc.data().name,
						"dept": doc.data().dept,
						"position": doc.data().position
					}
					this.employees.push(data)
					console.log(this.employees)
				});
			})
		}
	}
</script>

